@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: 'Lato', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.topnav {
    background-color: white;
    top: 0;
    width: 100%;
    box-sizing: border-box;
    position: sticky;
    position: -webkit-sticky;
    z-index: 100;
    opacity: .95;
    filter: alpha(opacity95); /* IE8 and lower */
    font-size: large;
}

.row {
    display: flex;
    padding: 20px 30px;
}
    
.column {
    flex: 33% 1;
}

.topnav-center {
    text-align: center;
    flex: 50% 1;
}

.topnav-left {
    flex: 25% 1;
}

.topnav-right {
    text-align: right;
    flex: 25% 1;
}

.topnav a{
    color: black;
    text-decoration: none;
}

.topnav a:hover{
    color: grey;
    text-decoration: none;
}

.topnav a.active {
    color: grey;
    text-decoration: none;
}

.fa {
    padding-left: 10px;
}

@media screen and (max-width: 600px) {
    .topnav {
        font-size: medium;
    }
}
/*--Scrolling Content-*/

ul.slides li.slide {
    outline: none;
    position: relative;
    z-index: 50;
    -webkit-transform: translate3d(0,0,0);
    margin:0 0;
    padding: 0;
    overflow: hidden;
    height: 90vh;
}

li.slide {
    display: list-item;
    text-align: -webkit-match-parent;
}

ul.slides {
-webkit-margin-before: 1em;
-webkit-margin-start: 0px;
-webkit-margin-end: 0px;
-webkit-padding-start: 40px;
list-style: none;
padding:0;
margin-bottom: 0;
height: 100%;
}

/*-- About Me --*/
.welcome {
    font-size: extra-large;
}


/*-- About Me --*/
.about-me {
background-color: #fff;
padding: 75px 0px;
position: relative;
width: 100%;
height: 100%;
z-index: 50;
pointer-events: auto;
font-size: large;
}

.profile-image {
position: absolute;
top: 50%;
-webkit-transform: translate(0%, -50%);
        transform: translate(0%, -50%);
width: 35%;
right: 50%;
margin-right: 15px;
}

@media screen and (max-width: 600px) {
.profile-image {
    position: relative;
    left:50%;
    top: 0%;
    width: 50%;
    height: auto;
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
    border-radius: 50%;
}
}

.header-text {
position: absolute;
width: 35%;
top: 50%;
left: 50%;
-webkit-transform: translate(0%, -50%);
        transform: translate(0%, -50%);
}

@media screen and (max-width: 600px) {
.header-text {
    position: relative;
    top: 0px;
    left: 50%;
    width: 100%;
    padding: 15px;
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
    text-align: center;
}
}

.header-text-center {
position: relative;
top: 50%;
width: 100%;
margin-left: 15px;
}

@media screen and (max-width: 600px) {
.header-text-center {
    margin-left: 0px;
}
}

.header-text-center h1 {
margin-top: 0px;
}

/*--Contact Section--*/

.card {
    left:50%;
    top:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    padding: 20px;
    position: relative;
    width: 50%;
    font-size: large;
}

@media screen and (max-width: 600px) {
.card {
    width: 100%;
}
}
.blog-list {
    display: flex;
    flex-wrap: wrap;
    margin: 2rem;
}
.page {
    margin-top: 1rem;
}
